.main {
  font-size: 16px;
  border: none;
  max-width: 320px;
  margin: 20px;
  padding: 20px;
  background-color: white;
  border-radius: 25px;
}

@media only screen
and (max-device-width : 400px) {
  .main {
    width: 100%;
    max-width: 100%;
    height: 100%;
    flex: 1;
    margin: 0px !important;
    padding: 0px;
    border-radius: 0px;
  }

  .header {
    margin-top: 20px;
    margin-bottom: 20px;
  }
}

.main * {
  color: #404040;
}

.exercise input {
  max-width: 50px;
  text-align: center;
  margin: 0 10px;
  font-size: 18px;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  font-weight: bold;
}


.settingsButton {
    margin: 0;
    padding: 5px;
    border-radius: 25px;
}

.settingsButton:hover {
    background-color: #e7e7e7;
}

.settingsButton:active {
    background-color: #e3e3e3 !important;
}

@-webkit-keyframes shake {
  0%,
  100% {
    -webkit-transform: translateX(0);
  }
  10%,
  30%,
  50%,
  70%,
  90% {
    -webkit-transform: translateX(-10px);
  }
  20%,
  40%,
  60%,
  80% {
    -webkit-transform: translateX(10px);
  }
}

@keyframes shake {
  0%,
  100% {
    transform: translateX(0);
  }
  10%,
  30%,
  50%,
  70%,
  90% {
    transform: translateX(-10px);
  }
  20%,
  40%,
  60%,
  80% {
    transform: translateX(10px);
  }
}

.shake {
  -webkit-animation-name: shake;
  animation-name: shake;
  background-color: pink !important;
}

.exercise {
  margin-top: 30px;
  margin-bottom: 20px;
  font-size: 32px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.error {
  background-color: pink !important;
}

.rainbowHr {
  border: 0;
  height: 1px;
  background-image: linear-gradient(to right, #f0f0f0, #00b9ff, #59d941, #f0f0f0);
}

.header {
  padding: 0 10px;
  display: flex;
  justify-content: space-between;
  color: #303030;
}

.statsContainer {
  display: flex;
}

.stat {
  display: flex;
  align-items: center;
  margin-left: 10px;
  font-size: 18px;
}

.stat svg {
  font-size: 14px;
  padding: 4px;
}

.keypad {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
}
.keypad button {
  flex-basis: calc(33% - 6 * 5px);
  margin: 10px;
  font-size: 16px;
  font-weight: bold;
}