.box {
  display: flex;
  align-items: center;
  user-select: none;
  padding: 10px 0;
}

.box input {
  position: absolute;
  opacity: 0;
  z-index: 2;
  transform: scale(2.5);
}

.box input:checked ~ .check {
  background: linear-gradient(30deg, rgba(63, 94, 251, 1) 0%, rgba(252, 70, 107, 1) 100%);
  transition-delay: 0.2s !important;
  transition: all 0.25s cubic-bezier(0, 1.05, 0.72, 1.07);
}

.box label {
  padding-left: 30px;
}

.check {
  width: 20px;
  height: 20px;
  border: 1px dotted black;
  position: absolute;
  border-radius: 25px;
  background-color: #fff;
  border: 2px solid black;
  box-shadow: 0px 0px 0px 0px #00ea90 inset;
  transition: all 0.15s cubic-bezier(0, 1.05, 0.72, 1.07);
}
